<template>
  <div class="message">
    <div class="message-info">
      <Panel>
        <template #header>
          <router-link to="/">主页</router-link>
          <span>/</span>
          <span>新消息</span>
        </template>
        <template #content>
          <div class="content">
            <div v-if="hasnot_read_messages.length">
              <p v-for="(item,index) in hasnot_read_messages" :key="index">
                <router-link :to="`/user/${item.author.loginname}`">{{item.author.loginname}}</router-link>在话题
                <router-link :to="`/post/${item.topic.id}`">{{item.topic.title}}</router-link>中@了你
              </p>
            </div>
            <p v-else>无消息</p>
          </div>
        </template>
      </Panel>
      <Panel>
        <template #header>过往信息</template>
        <template #content>
          <div v-if="has_read_messages.length">
            <p v-for="(item,index) in has_read_messages" :key="index">
              <router-link :to="`/user/${item.author.loginname}`">{{item.author.loginname}}</router-link>在话题
              <router-link :to="`/post/${item.topic.id}`">{{item.topic.title}}</router-link>中@了你
            </p>
          </div>
          <p v-else>无消息</p>
        </template>
      </Panel>
    </div>
    <AsideInfo />
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import AsideInfo from "../components/AsideInfo.vue";
export default {
  components: {
    Panel,
    AsideInfo
  },
  data() {
    return {
      has_read_messages: [],
      hasnot_read_messages: []
    };
  },
  async created() {
    const accesstoken = localStorage.getItem("token");
    const res = await this.$axios.get(`/messages?accesstoken=${accesstoken}`);
    this.has_read_messages = res.data.has_read_messages;
    this.hasnot_read_messages = res.data.hasnot_read_messages;
  }
};
</script>

<style lang='less'>
.message {
  display: flex;
  width: 100%;
  .message-info {
    flex-grow: 1;
  }
}
</style>